body {
  margin: 0;
  padding: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
    font-family: "Century Gothic";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  margin: 1em;
  background-color: #f6f6f6;
}

.App {
  font-family: "Century Gothic";
}
.slider_top {
    width: 100%;
}

.main_banner {
    height: 15vmin;
}

.top_banner {
    display: flex !important;
    align-items: center;
    padding: 0;
    height: inherit;
    background-image: url("/assets/top_banner.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.top_banner-text {
    display: flex;
    align-items: center;
    width: 18%;
    height: 40%;
    --AdaptiveFont: calc(2 * (100vw - 30rem) / 100 + 1rem); /* Universal formula for adaptive font */
    padding-left: 1%;
    margin-left: 5%;
    font-family: "Century gothic";
    font-size: var(--AdaptiveFont); 
    background-color: #424146;
    color: #ffea00;
}
.navbar-header {
    margin: 1.5em;
    font-family: "Century gothic";
}

.navbar-default {
    margin: 0;
    background-color: #f6f6f6;
}

.navbar-right > li > a {
    color: black;
    font-size: 1.313em;
    font-family: "Code Light";
    font-weight: 600;
    letter-spacing: 0.05em;
}

.navbar-right :hover {
    background-color: #ffea00 !important;
}

.navbar-toggle {
    border: 2px white solid;
}

.navbar-toggle > span {
    background-color: white;
}

.navbar-collapse > ul {
    margin-top: 54px;
}

@media only screen and (max-width: 768px){
    .navbar-header > a {
        display: none;
    }
}

.main-content {
    margin-top: 5vmin;
}

.slider {
    background-color: #424147;
}

.slider-resize_icon {
    position: absolute;
    right: 2%;
    top: 1%;
}

i {
    border: solid white;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 5px;
}

.slider-arrow-left {
    width: 4%;
    height: 5%;
    background-color: black;
    position: absolute;
    left: 0;
    top: 20%;
}

.slider-arrow-left-arr {
    position: absolute;
    top: 40%;
    left: 40%;
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
}

.slider-arrow-right {
    width: 4%;
    height: 5%;
    background-color: black;
    position: absolute;
    right: 0;
    top: 20%;
}

.slider-arrow-right-arr {
    position: absolute;
    top: 40%;
    right: 40%;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.slider-line {
    background-color: black;
    width: 100%;
    margin: 0;
    padding: 0;
    bottom: 0;
}

.slider-line-pics {
    height: inherit;
    width: inherit;
    display: flex;
    justify-content: space-between;
    margin: 0;
    overflow: hidden;
    padding-left: 3vmin;
    padding-right: 3vmin;
}

.slider-line-col {
    position: relative;
    display: inline-block;
    padding: 1px;
}

.slider-line-col:hover:after {
    content: '\A';
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 0, 0.4);
    border: 1px solid yellow;
    position: absolute;
    top: 0;
    left: 0;
}

.slider-line-col {
    cursor: pointer;
}

.project-description {
    width: 97%;
    margin: auto;
    padding-top: 2vmin;
    padding-bottom: 1.5vmin;
    border-bottom: 1px solid #cdcdcd;
}

.project-description-name {
    display: inline-block;
    background-color: #424147;
    color: #ffea00;
    text-transform: uppercase;
    width: 26%;
    text-align: center;
}

.project-description-text {
    padding-top: .8vmin;
    color: #424147;
}

.project-description-text > p {
    line-height: 1.7;
}

.recent-projects {
    width: 97%;
    margin: auto;
    padding-top: 2vmin;
    padding-bottom: 1.5vmin;
}

.recent-projects-name {
    display: inline-block;
    background-color: #424147;
    color: #ffea00;
    text-transform: uppercase;
    width: 26%;
    text-align: center;
}

.recent-projects-pics {
    height: inherit;
    padding-top: 3vmin;
    display: flex;
    justify-content: space-between;
    margin: -15px;
}

.recent-projects-img {
    margin: 0;
    height: inherit;
    cursor: pointer;
}

.recent-projects-col:nth-child(2){
    padding-right: 1vmin;
    padding-left: 1vmin;
}

.sidebar {
    float: right;
}

.project-details {
    background-color: #424147;
    width: 100%;
    margin: 0;
}

.project-details-content {
    padding: 2vmin;
}

.project-details-name {
    margin: 0;
    text-transform: uppercase;
    color: #ffea00;
    padding-bottom: 2.5vmin;
}

.project-details-menu {
    display: flex;
    align-items: flex-start;
    margin: 0;
    padding-bottom: 2vmin;
}

.project-details-pic-col {
    width: 10%;
    padding-right: 1vmin;
}

.project-details-description {
    width: 90%;
    color: white;
    text-transform: uppercase;
}

.popular-posts {
    background-color: #424147;
    width: 100%;
    margin: 0;
    padding-bottom: 2vmin;
}

.popular-posts-main {
    padding: 0 2vmin 2vmin 2vmin;
}

.popular-posts-image {
    padding: 0;
    width: 20%;
}

.popular-posts-info {
    width: 80%;
}

.popular-posts-name {
    margin: 0;
    color: #ffea00;
    text-transform: uppercase;
    padding-bottom: 2.5vmin;
}

.popular-posts-item {
    margin: 0;
}

.popular-posts-text {
    margin: 0;
    color: white;
    text-transform: uppercase;
    width: 100%;
}

.popular-posts-date {
    margin: 0;
    color: #ffea00;
    width: 100%;
    text-align: right;
}

.tags {
    background-color: #424147;
    width: 100%;
    margin: 0;
}

.tags-col {
    width: 100%;
    height: 100%;
    padding: 0 2vmin 2vmin 2vmin;
}

.tags-name {
    margin: 0;
    color: #ffea00;
    text-transform: uppercase;
    padding-bottom: 2.5vmin;
}

.tags-content {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
}

.tags-content-item {
    text-transform: uppercase;
    background-color: #ffea00;
    margin: .1vmin;
    padding: .1vmin .3vmin .1vmin .3vmin;
    font-weight: 600;
}
.footer-grid {
    margin: 0;
    padding: 7vmin 0 0 0;
    color: #ffffff;
}

.footer {
    background-color: #414146;
    width: 100%;
    margin: 0;
}

.footer-main {
    margin: 0;
    padding: 2.5vmin 10vmin 2.5vmin 10vmin;
}

.footer-about {
    margin: 0;
    padding: 0 2vmin 0 0;
}

.footer-about-name {
    font-size: 130%;
    margin: 0;
    padding: 0 0 2vmin 0;
}

.footer-about-desc {
    margin: 0;
    padding: 0 0 3vmin 0;
}

.footer-about-btn {
    display: inline-block;
    padding: 1vmin 2vmin 1vmin 2vmin;
    border-bottom: 1px solid #ffea00;
    cursor: pointer;
    margin: 0;
    background-color: black;
    
}

.footer-about-pics-name {
    margin: 0;
    padding: 3vmin 0 2vmin 0;
    font-size: 130%;
}

.footer-about-pics {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    max-width: 400px;
}

.footer-about-pics-item {
    padding: .2vmin;
}

.footer-tweets {
    padding: 0 2vmin 0 0;
    margin: 0;
}

.footer-tweets-name {
    margin: 0;
    font-size: 130%;
    padding: 0 0 2.5vmin 0;
}

.footer-tweets-items-row {
    margin: 0;
}

.footer-tweets-item {
    margin: 0;
}

.footer-tweets-item-icon {
    margin: 0;
    padding: 0;
}

.footer-tweets-item-desc {
    margin: 0;
    padding: 0;
}

.footer-tweets-item-desc-name {
    margin: 0;
}

.footer-tweets-item-tag {
    color: #ffea00;
    display: inline-block;
}

.footer-tweets-item-desc-date {
    margin: 0;
    font-size: 70%;
    color: #ffea00;
}

.footer-tweets-socials {
    display: flex;
    margin: 0;
    flex-wrap: wrap;
}

.footer-tweets-socials-name {
    font-size: 130%;
}

.footer-tweets-socials-item {
    padding: .1vmin;
    cursor: pointer;
}

.footer-contacts {
    margin: 0;
}

.footer-contacts-name {
    margin: 0;
    font-size: 130%;
    padding: 0 0 5vmin 0;
}

.footer-contacts-address {
    margin: 0;
    padding: 0 0 2vmin 0;
}

.footer-contacts-address-icon,
 .footer-contacts-phone-icon,
  .footer-contacts-mail-icon {
    max-width: 30px;
    padding: 0;
}

.footer-contacts-address-text {
    padding: 0 0 0 1vmin;
}

.footer-contacts-phone-text {
    padding: 0 0 0 1vmin;
    max-width: 50px;
}

.footer-contacts-phone {
    margin: 0;
    padding: 0 0 2vmin 0;
}

.footer-contacts-phone-number {
    padding: 0 0 0 .5vmin;
    margin: 0;
}

.footer-contacts-mail-text-tag {
    display: inline-block;
    color: #ffea00;
}

.footer-contacts-mail {
    margin: 0;
}

.footer-contacts-mail-text {
    padding: 0 0 0 1vmin;
}

.footer-contacts-form {
    margin: 0;
}

.footer-contacts-form-name {
    margin: 0;
    font-size: 130%;
    padding: 2.5vmin 0 2vmin 0;
}

.footer-contacts-form-text {
    margin: 0;
    padding: 0 0 2vmin 0;
}

.footer-contacts-form-input {
    margin: 0;
}

.footer-contacts-form-full {
    display: inline-block;
}

.footer-contacts-form-img {
    display: inline-block;
}

input[type=text] {
    background-color: #414146;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #ffea00;
    outline: none;
}

.footer-copyright {
    display: flex;
    align-items: center;
    background-color: black;
    text-align: center;
    width: 100%;
    height: 5vmin;
    margin: 0;
    padding: 0;
}

.footer-copyright-text {
    font-size: 90%;
}

.footer-copyright-nav {
    display: flex;
    justify-content: space-between;
}

.footer-copyright-nav-item {
    cursor: pointer;
    text-transform: uppercase;
}
